.active{
  color: rgb(175, 127, 68);
  text-decoration: none;
  background-color: white;

}

nav {
  display: flex;
  align-items: flex-start;
  align-self:flex-start;
  z-index: 10;
  align-content: flex-start;
}

nav ul {
  display: flex;
  align-items: flex-start;
}

nav ul li {
  list-style: none;
  text-align: center;
}

nav ul li a {
  display: block;
  text-decoration: none;
  color: #000000;
  font-size: medium;
  font-family: "Playfair Display", serif;
  font-optical-sizing: auto;
  font-weight: 500;
  letter-spacing: 0.2cqb;
  font-style: normal;
  margin-left: 2rem;
  margin-right: 1rem;
}

nav ul li a:not(.active):hover {
  color: rgb(154, 154, 120);
}


.menu {
  display: none;
  flex-direction: column;
  align-items: flex-start;
  align-content: flex-start;
  color: #000000;
}

@media (max-width: 1100px) {
  .menu {
    display: flex;
    align-content: flex-start;
    margin: 1.25rem;
  }

  nav .title {
    font-size: 2rem;
  }

  nav {
    flex-direction: column;
    align-items: start;
    align-content: flex-start;
    border-radius: 2.5rem;
  }

  nav ul.open {
    display: flex;
  }

  nav ul {
    display: none;
    flex-direction: column;
    width: 100%;
    margin-bottom: 0.25rem;
  }

  nav ul li {
    width: 100%;
    text-wrap: center;
  }

  nav ul li a {
    margin: 0.2rem 0.5rem;
  }
}

@media (max-width: 530px) {
  nav .title {
    font-size: 1.5rem;
  }

  .menu {
    height: 1rem;
  }
}
